//sessionStorage
export const storage = {
    get(key) {
        if (key == null || typeof key !== "string") {
            return ""
        }
        try {
            return JSON.parse(sessionStorage.getItem(key)) || "";
        } catch (e) {
            return sessionStorage.getItem(key) || ""
        }
    },
    set(key, value) {
        if (value == null) {
            return sessionStorage.removeItem(key);
        }
        sessionStorage.setItem(key, JSON.stringify(value));
        return value;
    },
    remove(key){
        console.log(key)
        return sessionStorage.removeItem(key);
    }

};

//isPC
export function isPC() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"];
    var flag = true;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
        }
    }
    return flag;
}


/**
 * .
 *
 * @param num (NumberString)
 * @param cent (Number)
 * @param isThousand  0:,1:();
 * @return ,'1,234,567.45'
 * @type String
 */
export function formatNumber(num, cent, isThousand) {
    if (typeof num == 'string') {
        num = num.replace(/\$|\,/g, '');
    }
    //num = num.toString().replace(/\$|\,/g, '');

    // 
    if (isNaN(num))
        num = "0";

    // (/)
    let sign = (num == (num = Math.abs(num)));

    num = Math.floor(num * Math.pow(10, cent) + 0.50000000001); // .
    let cents = num % Math.pow(10, cent); // 
    num = Math.floor(num / Math.pow(10, cent)).toString(); // 
    cents = cents.toString(); // ,

    // 
    while (cents.length < cent)
        cents = "0" + cents;

    if (isThousand) {
        // .
        for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++)
            num = num.substring(0, num.length - (4 * i + 3)) + ',' + num.substring(num.length - (4 * i + 3));
    }

    if (cent > 0)
        return (((sign) ? '' : '-') + num + '.' + cents);
    else
        return (((sign) ? '' : '-') + num);
}

String.prototype.trim = function () {
    return this.replace(/(^\s*)|(\s*$)/g, "");
}

//cookie
export function getCookie(name) {
    var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    if (arr = document.cookie.match(reg))
        return (arr[2]);
    else
        return null;
}

//cookie
export function setCookie(c_name, value, expiredays) {
    var exdate = new Date();
    exdate.setDate(exdate.getDate() + expiredays);
    document.cookie = c_name + "=" + escape(value) + ((expiredays == null) ? "" : ";expires=" + exdate.toGMTString());
};

String.prototype.bool = function() {
    return (/^true$/i).test(this);
};
